ul {
  margin: 0;
  list-style: none;
  padding: 0; }

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

.course {
  grid-area: course; }

.course_period {
  margin: 0 0 0.3rem 0;
  color: #21baab; }

.education {
  grid-area: education; }

.education_period {
  margin: 0 0 0.3rem 0;
  color: #21baab; }

.education_center {
  margin-bottom: 1.2rem; }

.experience {
  grid-area: experience;
  padding-top: 1rem; }

.experience_work {
  margin: 0 0 0.5rem 0;
  font-size: 1rem; }

.experience_period {
  margin: 0 0 0.3rem 0;
  color: #21baab; }

.experience_duties {
  margin: 0 0 2rem 0; }

.experience_duties li::before {
  content: "•";
  color: #21baab;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  padding-right: 10px; }

@media (min-width: 1200px) {
  .experience {
    padding-top: 0; } }

.interest {
  grid-area: interest; }

.interest ul {
  margin-bottom: 1rem; }

.interest li {
  display: flex;
  justify-content: left;
  margin-bottom: 0.3rem; }

.interest li:hover {
  color: #21baab; }

.interest svg {
  margin-right: 1.5rem; }

.interest svg:hover {
  fill: #21baab; }

@media (min-width: 1200px) {
  .interest ul {
    margin-bottom: 2rem; }
  .interest svg {
    margin-left: 0; } }

.language {
  grid-area: language;
  margin-bottom: 1rem; }

.language li {
  display: inline-block;
  background-color: #21baab;
  color: white;
  margin: 0.2rem;
  padding: 0.6rem;
  border-radius: 5%; }

.language li:hover {
  background-color: black; }

.name {
  grid-area: name;
  display: flex;
  align-items: center;
  flex-direction: column-reverse;
  justify-content: flex-start;
  border-bottom: 5px solid #21baab;
  text-align: center;
  flex-wrap: wrap; }

.presentation p {
  text-align: justify;
  font-size: 0.6rem; }

.photo_image {
  border: 5px solid #21baab;
  border-radius: 50%;
  margin-bottom: 1rem; }

.contact {
  grid-area: contact;
  border-bottom: 5px solid #21baab; }

.contact li {
  list-style: none;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin-bottom: 0.3rem; }

.contact ul {
  margin-bottom: 1rem; }

.contact li:hover {
  color: #21baab; }

.contact svg {
  margin-right: 0.5rem; }

.contact svg:hover {
  fill: #21baab; }

@media (min-width: 900px) {
  .contact {
    border-bottom: none;
    margin: 3rem; }
  .contact h4 {
    display: none; }
  .presentation p {
    text-align: justify;
    font-size: 1rem; } }

@media (min-width: 1200px) {
  .name {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    grid-area: name;
    border-bottom: 5px solid #21baab;
    padding-bottom: 1.3rem; }
  .contact {
    grid-area: contact;
    border-bottom: 5px solid #21baab;
    padding-left: 3rem;
    padding-top: 1.7rem;
    padding-bottom: 4.5rem; }
  .presentation {
    width: 60%; }
  .contact li {
    justify-content: flex-end; }
  .contact svg {
    margin-left: 0.5rem; } }

.resume {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: "name"  "experience" "skill" "education" "course" "language" "interest" "contact";
  align-items: center;
  margin: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 0.8rem;
  align-self: center; }

.visually-hidden {
  visibility: hidden; }

html {
  font-size: 16px; }

h1 {
  margin: 0;
  line-height: 0;
  font-size: 0; }

h2 {
  font-size: 1.5rem;
  font-weight: 200;
  margin: 0; }

h3 {
  font-size: 1rem;
  color: #21baab;
  font-weight: 100;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

a {
  text-decoration: none;
  color: black; }

h4 {
  text-transform: uppercase;
  font-size: 1.2rem;
  margin-top: 0.9rem;
  margin-bottom: 0.9rem; }

h5 {
  font-size: 1rem;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 0.7rem; }

@media (min-width: 900px) {
  .resume {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "name contact" "experience skill" "education course" "language interest";
    align-items: flex-start;
    font-size: 1rem; } }

@media (min-width: 1200px) {
  .resume {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 2fr 1fr;
    grid-template-areas: "name contact" "experience skill" "education course" "language interest";
    padding-left: 7rem;
    padding-right: 7rem; }
  h2 {
    font-size: 2rem;
    font-weight: 200;
    margin: 0; }
  h3 {
    font-size: 1.5rem;
    color: #21baab;
    font-weight: 100;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem; } }

.signature {
  box-sizing: border-box;
  margin: 0 2rem 2rem 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 0.5rem; }

.signature_text {
  margin: 0;
  padding: 0;
  text-align: justify; }

@media (min-width: 900px) {
  .signature {
    margin: 0;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 0.8rem; }
  .signature_text {
    text-align: justify;
    text-indent: 20px; } }

.skill {
  grid-area: skill; }

.skill ul {
  margin-bottom: 2rem; }

.skill li {
  display: inline-block;
  background-color: #21baab;
  color: white;
  margin-top: 0.3rem;
  margin-right: 0.5rem;
  margin-bottom: 0.3rem;
  padding: 0.8rem;
  border-radius: 5%; }

.skill li:hover {
  background-color: black; }
