.interest{
    grid-area: interest;
}
.interest ul {
    margin-bottom: 1rem;
}
.interest li {
    display: flex;
    justify-content: left;
    margin-bottom: 0.3rem;
}
.interest li:hover{
    color: $green;
  }
.interest svg{
    margin-right: 1.5rem;
  }
.interest svg:hover{
    fill: $green;
}
@media (min-width: 1200px) {
    .interest ul {
        margin-bottom: 2rem;
    }

    .interest svg{
        margin-left: 0;
      }
  }